import { Component, NgZone, ChangeDetectorRef} from '@angular/core';
import { NavController, NavParams, AlertController, LoadingController } from 'ionic-angular';
import { Connect } from '../../providers/connect';
import { BarcodeScanner } from '@ionic-native/barcode-scanner';

/**
 * Generated class for the StockpPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-picker',
  templateUrl: 'picker.html',
})
export class PickerPage {

  prodlist : any[];
  orderno : any = '';
  pstatus : any;
  thissku : any = '';
  thisname : any = '';
  thisqty : any = '';
  thisid : any;
  thisloc : any;
  barsku : any = '';
  allgood: any = 0;

  constructor(public navCtrl: NavController, public navParams: NavParams, private connect: Connect, public loadingCtrl : LoadingController, private zone: NgZone, public alertCtrl: AlertController, public cdr: ChangeDetectorRef, public barcodeScanner : BarcodeScanner) {
     this.orderno=this.navParams.get("orderno");
     this.pstatus=0;
     this.prodlist=[];
     this.barsku='';
     this.allgood = 0;
  }

  ionViewDidLoad() {
        let that=this;
            let loader = this.loadingCtrl.create({
	      content: "Loading..."
	    });
	    loader.present();

        let url = 'pickdetails.php?id='+this.orderno;
        this.connect.getList(url).subscribe(
         data => {
           loader.dismiss();
           that.zone.run(() => {
             that.prodlist = data;             
           });
         },
         err => {
            loader.dismiss();
            this.connect.logError(err)
         }
        ); 
     
  }

  lookup() {
     this.findProd(this.barsku);
  }

  scanBar() {
    this.barcodeScanner.scan().then((barcodeData) => {
       if (!barcodeData.cancelled) {
          this.findProd(barcodeData.text);
       }
    }, (err) => {
    });
  }
  
  findProd(s) {
        let that=this;
            let loader = this.loadingCtrl.create({
	      content: "Searching..."
	    });
	    loader.present();

        let url = 'spitem.php?code='+s;
        this.connect.getList(url).subscribe(
         data => {
           loader.dismiss();
           that.zone.run(() => {
             if (data.result > 0) {
               let found  = 0;
               for ( var i in that.prodlist) {
                if (that.prodlist[i].id == data.id) {
                  that.thissku = data.SKU;
                  that.thisname = data.name;
                  that.thisid = data.id;
                  that.barsku='';
                  that.thisqty='';
                  that.pstatus=1;
                  found = 1;
                }
               }
               if (found ==0) alert('Product does not belong on this order');
             }
             
           })
         },
         err => {
            loader.dismiss();
            this.connect.logError(err)
         }
        ); 
  }

  cancelit()
  { this.pstatus=0;
  }
  
  saveit()
  {  let that=this;
     let q = 1;
     let ag = 1;
     if (this.thisqty.length) q=parseInt(this.thisqty);
     that.zone.run(() => {
       for (var i in that.prodlist) {
         if (that.prodlist[i].id == that.thisid) 
            that.prodlist[i].counted += q;        
         if (that.prodlist[i].counted == that.prodlist[i].expected)
              that.prodlist[i].backcolor = 'lightgreen';
         else {
              that.prodlist[i].backcolor = 'khaki';
              ag = 0;
         }
       }
       this.allgood = ag;
       this.pstatus=0;
     });
  }

  kin(a)
  { this.thisqty = '' + this.thisqty + a;
  }

  allcomplete()
  {  

       let that=this;
            let loader = this.loadingCtrl.create({
	      content: "Saving..."
	    });
	    loader.present();

        let url = 'pickcomplete.php?id='+this.orderno;
        this.connect.getList(url).subscribe(
         data => {
           loader.dismiss();
           that.zone.run(() => {
             that.navCtrl.pop();             
           });
         },
         err => {
            loader.dismiss();
            this.connect.logError(err)
         }
        ); 
  }

}



