import { Component, NgZone, ChangeDetectorRef } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { Connect } from '../../providers/connect';
import { PickerPage } from '../picker/picker';


@Component({
  selector: 'page-pick',
  templateUrl: 'pick.html',
})
export class PickPage {

 public companies : any [];
 public orders : any [];
 public companyname: any;
 public company : any = 0;

  constructor(public navCtrl: NavController, public navParams: NavParams, public connect : Connect, private zone: NgZone, public cdr: ChangeDetectorRef) {
     this.companies = [];
     this.orders = [];
     this.company = 0;
     this.companyname = "";
  }

  ionViewDidEnter() {
     this.refresh();
  }

  refresh() {
    this.companies = [];
    let that = this;
    let url = 'allcompanies.php';

    this.connect.getList(url).subscribe(
      data => {
        that.zone.run(() => {
          that.companies = data;
          that.cdr.markForCheck();
        });
      },
      err => this.connect.logError(err)
    );
  }

  goComp(c) {
    this.companyname=c.name;
    this.company = c.id;
    this.orders = [];
    let that = this;
    let url = 'pickorders.php?id='+c.id;

    this.connect.getList(url).subscribe(
      data => {
        that.zone.run(() => {
          that.orders = data;
          that.cdr.markForCheck();
        });
      },
      err => this.connect.logError(err)
    );
  }  

  goBack() {
    this.company=0;
  }

  goOrd(o) {
     this.navCtrl.push ( PickerPage, { 'orderno' : o.id });
     
  }
}



